import React, { useEffect } from 'react'
import { useNavigate } from 'react-router-dom';
import { useResetRecoilState } from 'recoil';
import LoadingModal from '../../components/UI/Atoms/Spinner/LoadingModal';
import assoInfoAtom from '../../recoil/atom/association/assoInfoAtom';
import dojangIdAtom from '../../recoil/atom/dojangIdAtom';
import dojangInfoAtom from '../../recoil/atom/dojangInfoAtom';
import dojangListAtom from '../../recoil/atom/dojangListAtom';
import userInfoAtom from '../../recoil/atom/login/userInfoAtom';

const Logout = () => {
  const navigate = useNavigate()

  const resetDojangId = useResetRecoilState(dojangIdAtom)
  const resetDojangInfo = useResetRecoilState(dojangInfoAtom)
  const resetDojangList = useResetRecoilState(dojangListAtom)
  const resetUserInfo = useResetRecoilState(userInfoAtom)
  const resetAssoInfo = useResetRecoilState(assoInfoAtom)

  useEffect(() => {
    sessionStorage.removeItem('refresh_token')
    sessionStorage.removeItem('user_role')
    sessionStorage.removeItem('access_token')

    resetDojangId()
    resetDojangInfo()
    resetDojangList()
    resetUserInfo()
    resetAssoInfo()

    setTimeout(() => {
      navigate('/')
      // window.location.reload()
    }, 200)
  }, [])
  
  return (
    <LoadingModal />
  )
}

export default Logout